















































































import {Component, Prop, PropSync, Vue} from 'vue-property-decorator'
import store from '@/store/store'
import settings from '@/store/modules/settings'

@Component
export default class SettingsBlock extends Vue {
  @Prop() public readonly translateTimerSecondsPassed!: number
  @Prop() public readonly showTimerSettings!: boolean
  @PropSync('passedAllDots', {type: Boolean, default: false}) isPassedAllDots!: boolean
  @PropSync('coloredDots', {type: Boolean, default: false}) coloredDotsEnabled!: boolean

  settingsPopoverOpened: boolean = false
  id: number = Math.floor(Math.random() * 100000)


  get wordChangeInterval(): number {
    return store.state.wordChangeInterval
  }

  get translationDirection(): string {
    return settings.translationDirection
  }

  get translateTimerInterval(): number {
    return settings.translateTimerInterval
  }

  get soundEnabled() {
    return settings.soundEnabled
  }

  get translationDisabled() {
    return settings.translationDisabled
  }

  setDisableTranslation() {
    settings.setTranslationDisabled(!settings.translationDisabled)
  }

  setTranslationDirection(event: any) {
    settings.setTranslationDirection(event.target.value)
  }

  setWordChangeInterval(event: any) {
    store.dispatch('setWordChangeInterval', parseInt(event.target.value, 10))
  }

  toggleSoundEnabled() {
    settings.setSoundEnabled(!settings.soundEnabled)
  }

  setTranslateTimerInterval(event: any) {
    settings.setTranslateTimerInterval(parseInt(event.target.value, 10))
  }

  changeTranslateTimerInterval() {
    settings.setTranslateTimerInterval(settings.translateTimerInterval + 1)
  }

  toggleFullscreen() {
    store.dispatch('setFullscreenMode', !store.state.useFullscreen)
  }

  get translationType(): string {
    return settings.translationType
  }

  setTranslationType(event: any) {
    settings.setTranslationType(event.target.value)
  }

  get dotsCount() {
    if (!this.coloredDotsEnabled) {
      return this.translateTimerInterval
    } else {
      return 20
    }
  }

  dotClass(i: number) {
    let result = ''
    if (i <= this.translateTimerSecondsPassed || this.isPassedAllDots) {
      result += 'passed'
    }
    if (i <= 5) {
      result += ' green'
    } else if (i <= 10) {
      result += ' yellow'
    } else if (i <= 20) {
      result += ' red'
    }
    return result
  }

  settingsPopoverToggle (show: boolean = true){
    this.settingsPopoverOpened = show;
  }

}
