import {WordData} from '@/store/types'
// @ts-ignore
import {Howl} from 'howler'
import settings from '@/store/modules/settings'

export default class Word {
  originalWord: string = ''
  originalTranslation: string = ''
  gender: string = 'm'
  partOfSpeech: string = ''
  plural: boolean = false
  pk: number = 0
  banned: boolean = false
  soundOfOriginalWord: Howl | null
  soundOfOriginalTranslation: Howl | null

  unifiedWord: string | null = ''
  unifiedTranslation: string | null = ''
  unifiedWordPollyUrl: string | null = ''
  unifiedTranslationPollyUrl: string | null = ''
  soundOfUnifiedWord: Howl | null
  soundOfUnifiedTranslation: Howl | null

  constructor(wordData: WordData | null) {
    if (!wordData) {
      return
    }

    this.originalWord = wordData.word
    this.gender = wordData.gender
    this.partOfSpeech = wordData.partOfSpeech
    this.plural = wordData.plural
    this.pk = wordData.pk
    this.originalTranslation = wordData.translations && wordData.translations[0] ?
      wordData.translations[0].translation : ''
    this.unifiedWord = wordData.unifiedWord
    this.unifiedTranslation = wordData.unifiedTranslation
    this.unifiedWordPollyUrl = wordData.unifiedWordPollyUrl
    this.unifiedTranslationPollyUrl = wordData.unifiedTranslationPollyUrl
    this.loadSingleSound(wordData.unifiedWordPollyUrl ?
        wordData.unifiedWordPollyUrl : require('@/assets/sounds/silence.mp3'))
        .then(howl => this.soundOfUnifiedWord = howl)

    this.loadSingleSound(wordData.unifiedTranslationPollyUrl ?
        wordData.unifiedTranslationPollyUrl : require('@/assets/sounds/silence.mp3'))
        .then(howl => this.soundOfUnifiedTranslation = howl)

    this.loadSingleSound(wordData.pollyUrl ?
        wordData.pollyUrl : require('@/assets/sounds/silence.mp3'))
        .then(howl => this.soundOfOriginalWord = howl)

    this.loadSingleSound(wordData.translations && wordData.translations[0] ?
      wordData.translations[0].pollyUrl : require('@/assets/sounds/silence.mp3'))
      .then(howl => {
        this.soundOfOriginalTranslation = howl
      })
  }

  get isReady(): boolean {
    return (typeof this.soundOfWord !== 'undefined'
        && this.soundOfWord.state() === 'loaded'
        && typeof this.soundOfTranslation !== 'undefined'
        && this.soundOfTranslation.state() === 'loaded')
  }

  get name() {
    if (settings.translationType === 'original' || !this.unifiedWord) {
      return this.originalWord
    } else {
      return this.unifiedWord
    }
  }

  get translation() {
    if (settings.translationType === 'original' || !this.unifiedTranslation) {
      return this.originalTranslation
    } else {
      return this.unifiedTranslation
    }
  }

  get soundOfWord(): Howl {
    if (settings.translationType === 'original' || !this.unifiedWord) {
      return this.soundOfOriginalWord
    } else {
      return this.soundOfUnifiedWord
    }
  }

  get soundOfTranslation() {
    if (settings.translationDisabled){
      return new Howl({
        src: [require('@/assets/sounds/silence.mp3')],
        loop: false,
      })
    }
    if (settings.translationType === 'original' || !this.unifiedTranslation) {
      return this.soundOfOriginalTranslation
    } else {
      return this.soundOfUnifiedTranslation
    }
  }

  protected loadSingleSound(soundUrl: string) {
    const silence = new Howl({
      src: [require('@/assets/sounds/silence.mp3')],
      loop: false,
    })

    if (!soundUrl) {
      return new Promise(resolve => {
        resolve(silence)
      })
    } else {
      return new Promise((resolve, reject) => {
        const howl = new Howl({
          src: [soundUrl],
          preload: true,
          loop: false,
          buffer: true,
          html5: false,
          onload: () => resolve(howl),
          onloaderror: () => {
            resolve(silence)
          }
        })
      })
    }
  }
}
