























































































































import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import wordsStore from '@/store/modules/words'
import { DictionaryWord } from '@/classes/DictionaryWord'
import BlankCard from '@/components/BlankCard.vue'
import store from '@/store/store'
// @ts-ignore
import fullscreen from 'vue-fullscreen'
// @ts-ignore
import RadialProgressBar from 'vue-radial-progress'
import { ILesson } from '@/store/types'
import lessonsStore from '@/store/modules/lessons'
import DictionaryModeSetter from '@/components/DictionaryModeSetter.vue'
import LearnWordCard from '@/components/Words/LearnWordCard.vue'

Vue.use(fullscreen)

@Component({
  components: {
    LearnWordCard,
    RadialProgressBar,
    BlankCard,
    DictionaryModeSetter
  },
})

export default class LearnWordsDictionary extends Vue {
  wordsToLearn: DictionaryWord[] = []
  lessonComplete: boolean = false
  lessonIsLoading: boolean = true
  askForWordBan: boolean = false
  lastWordShownTimestamp: number = 0
  tooEarlySelfCheckMessage: boolean = false

  get error () {
    return wordsStore.errors.length ? wordsStore.errors[0] : null
  }

  get allWordsIsBanned () {
    return this.wordsToLearn.length === 0 && !this.lessonIsLoading
  }

  get learntWordsCount (): number {
    if (!this.wordsToLearn || !this.wordsToLearn.length) {
      return 0
    }

    return this.wordsToLearn.filter(({ learnt }) => learnt === true).length
  }

  get rememberedWordsCount (): number {
    return this.wordsToLearn.filter(({ remember }) => remember === true).length
  }

  get currentLesson (): ILesson {
    return store.state.currentLesson!
  }

  get isLessonLearnt () {
    return this.currentLesson.wordsLearned >= this.currentLesson.wordsCount
  }

  get currentWordToLearn (): DictionaryWord {
    return this.wordsToLearn[this.currentWordNumber]
  }

  get wordsCount () {
    if (!this.wordsToLearn || !this.wordsToLearn.length) {
      return 0
    }

    return this.wordsToLearn.length
  }

  get lessonProgress () {
    return 100.0 / (this.wordsCount - 1) * (this.currentWordNumber)
  }

  get currentWordNumber () {
    this.lastWordShownTimestamp = (new Date()).getTime()
    return store.state.wordNumber
  }

  get useFullscreen () {
    return store.state.useFullscreen
  }

  get myWordsLinkPopoverContent () {
    if (!this.currentLesson.isAuthenticated){
      return 'Необходимо залогиниться'
    }
  }

  async initDictinonary (wordsToLearn: DictionaryWord[]) {
    this.wordsToLearn = wordsToLearn
    this.lessonIsLoading = true
    await this.$nextTick()
    this.lessonComplete = false
    await store.dispatch('setWordsCount', this.wordsToLearn.length)
    this.setWordNumber(0)
    this.lessonIsLoading = false
    if (store.state.isDevEnv) {
      // console.log('Learn words dictionary loaded')
    }
  }

  setDictionaryMode (dictionaryMode: string, checkLearntWordsCount: boolean = true) {
    if (dictionaryMode === store.state.dictionaryMode) {
      return
    }

    if (checkLearntWordsCount && this.learntWordsCount !== this.wordsCount){
      this.tooEarlySelfCheckMessage = true
      return
    }

    store.dispatch('setDictionaryMode', dictionaryMode)
  }

  @Watch('lessonComplete')
  eventLessonComplete () {
    lessonsStore.loadLessons()
  }

  askForBan () {
    this.askForWordBan = true
  }

  cancelWordBan () {
    this.askForWordBan = false
  }

  banWord () {
    this.currentWordToLearn.banWord(this.currentLesson.isAuthenticated)
    this.wordsToLearn = this.wordsToLearn.filter(wordToLearn => wordToLearn !== this.currentWordToLearn)
    store.dispatch('setWordsCount', this.wordsToLearn.length)
    if (store.state.wordNumber >= this.wordsToLearn.length) {
      this.setWordNumber(0)
    }
    this.askForWordBan = false
  }

  setWordNumber (wordNumber: number) {
    store.dispatch('setWordNumber', wordNumber)
  }

  @Watch('useFullscreen')
  useFullscreenToggle () {
    (this.$refs.fullscreen as fullscreen).toggle()
  }

  showWordTranslation () {
    store.dispatch('showWordTranslation', true)
  }

  tooEarlySelfCheckContinue (c: boolean) {
    console.log(`Continue: ${c}`)
    if (c) {
      this.setDictionaryMode('self-check', false)
    }
    this.tooEarlySelfCheckMessage = false
  }
}
