






















































































































































import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import WordCard from '@/components/WordCard.vue'
import wordsStore from '@/store/modules/words'
import { DictionaryWord } from '@/classes/DictionaryWord'
import BlankCard from '@/components/BlankCard.vue'
import store from '@/store/store'
// @ts-ignore
import fullscreen from 'vue-fullscreen'
// @ts-ignore
import RadialProgressBar from 'vue-radial-progress'
import { ILesson } from '@/store/types'
import lessonsStore from '@/store/modules/lessons'
import DictionaryModeSetter from '@/components/DictionaryModeSetter.vue'
import SelfCheckWordCard from '@/components/Words/SelfCheckWordCard.vue'

Vue.use(fullscreen)

@Component({
  components: {
    SelfCheckWordCard,
    DictionaryModeSetter,
    WordCard,
    RadialProgressBar,
    BlankCard
  },
})

export default class SelfCheckWordsDictionary extends Vue {
  wordsToLearn: DictionaryWord[] = []
  lessonComplete: boolean = false
  lessonIsLoading: boolean = false
  askForWordBan: boolean = false
  lastWordShownTimestamp: number = 0
  isVeryLongDelay: boolean = false
  customGradeValue: number | null = null
  timerSecondsPassed: number = 0
  isStarted: boolean = false

  get customGrade () {
    return this.customGradeValue
  }

  setCustomGrade (value: number) {
    this.customGradeValue = value
  }

  get error () {
    return wordsStore.errors.length ? wordsStore.errors[0] : null
  }

  get learntWordsCount (): number {
    if (!this.wordsToLearn || !this.wordsToLearn.length) {
      return 0
    }

    return this.wordsToLearn.filter(({ remember }) => remember === true).length
  }

  get checkedWordsCount (): number {
    if (!this.wordsToLearn || !this.wordsToLearn.length) {
      return 0
    }

    return this.wordsToLearn.filter(({ remember }) => remember !== null).length
  }

  get rememberedWordsCount (): number {
    return this.wordsToLearn.filter(({ remember }) => remember === true).length
  }

  get currentLesson (): ILesson {
    return store.state.currentLesson!
  }

  get isLessonLearnt () {
    return this.currentLesson.wordsLearned >= this.currentLesson.wordsCount
  }

  get hintsShown () {
    const wordsHintsShown = this.currentWordToLearn.wordCharacters
      .reduce((accumulator, currentValue) => {
        return accumulator + (currentValue.shownAsHint ? 1 : 0)
      }, 0)

    const translationHintsShow = this.currentWordToLearn.translationCharacters
      .reduce((accumulator, currentValue) => {
        return accumulator + (currentValue.shownAsHint ? 1 : 0)
      }, 0)

    return wordsHintsShown + translationHintsShow
  }

  get currentWordToLearn (): DictionaryWord {
    return this.wordsToLearn[this.currentWordNumber]
  }

  get wordsCount () {
    if (!this.wordsToLearn || !this.wordsToLearn.length) {
      return 0
    }

    return this.wordsToLearn.length
  }

  get lessonProgress () {
    return 100.0 / (this.wordsCount - 1) * (this.rememberedWordsCount)
  }

  get currentWordNumber () {
    this.lastWordShownTimestamp = (new Date()).getTime()
    return store.state.wordNumber
  }

  get useFullscreen () {
    return store.state.useFullscreen
  }

  get myWordsLinkPopoverContent () {
    if (!this.currentLesson.isAuthenticated){
      return 'Необходимо залогиниться'
    }
  }

  async initDictinonary (wordsToLearn: DictionaryWord[]) {
    this.wordsToLearn = wordsToLearn
    // console.log('Init self check dictionary')
    this.lessonIsLoading = true
    await this.$nextTick()
    if (this.learntWordsCount === this.wordsToLearn.length) {
      this.lessonComplete = true
    } else {
      this.lessonComplete = false
      let i = 0
      for (; i < this.wordsToLearn.length; i++) {
        if (!this.wordsToLearn[i].remember) {
          break
        }
      }
      // console.log(i)

      this.setWordNumber(i)
    }

    await store.dispatch('setWordsCount', this.wordsToLearn.length)
    await this.waitWhileWordsReady()
    this.lessonIsLoading = false
  }

  sleep (ms: number) {
    return new Promise(resolve => setTimeout(resolve, ms))
  }

  async waitWhileWordsReady(): Promise<void> {
    const fourSlice = this.wordsToLearn.slice(0, 3)
    for (let i = 0; i < fourSlice.length; i++) {
      while (!this.wordsToLearn[i].word.isReady) {
        await this.sleep(1000)
      }
    }
    return
  }

  setDictionaryMode (dictionaryMode: string) {
    if (dictionaryMode === store.state.dictionaryMode) {
      return
    }

    store.dispatch('setDictionaryMode', dictionaryMode)
  }

  @Watch('lessonComplete')
  eventLessonComplete () {
    lessonsStore.loadLessons()
  }

  askForBan () {
    this.askForWordBan = true
  }

  cancelWordBan () {
    this.askForWordBan = false
  }

  banWord () {
    this.currentWordToLearn.banWord(this.currentLesson.isAuthenticated)
    this.wordsToLearn = this.wordsToLearn.filter(wordToLearn => wordToLearn !== this.currentWordToLearn)
    store.dispatch('setWordsCount', this.wordsToLearn.length)
    if (store.state.wordNumber >= this.wordsToLearn.length) {
      this.setWordNumber(0)
    }
    this.askForWordBan = false
  }

  sendCustomGrade() {
    const customGrade = this.customGrade
    if (customGrade !== null && customGrade >= 0 && customGrade <= 5) {
      this.setWordMemoryState(customGrade > 0, customGrade)
      this.isVeryLongDelay = false
    }
  }

  setWordNumber (wordNumber: number) {
    store.dispatch('setWordNumber', wordNumber)
  }

  setWordMemoryState (remember: boolean, customGrade: null | number = null) {
    if (!this.currentWordToLearn) {
      return
    }

    if (this.rememberedWordsCount === this.wordsCount) {
      this.lessonComplete = true
      return
    }

    this.currentWordToLearn.remember = remember
    const delay = this.timerSecondsPassed * 1000
    if (delay > 20000 && remember && !this.isVeryLongDelay && !this.hintsShown) {
      this.isVeryLongDelay = true
      this.setCustomGrade(0)
      return
    }
    if (this.currentLesson.isAuthenticated) {
      this.currentWordToLearn.updateWordState(delay, customGrade)
        .then(() => {
          store.dispatch('loadRepeatWordsCount')
        })
    }
    if (!remember) {
      this.currentWordToLearn.unrevealWord()
    }

    let wordNumberSet = false
    for (let i = this.currentWordNumber + 1; i < this.wordsCount; i++) {
      if (this.wordsToLearn[i].remember !== true) {
        this.setWordNumber(i)
        wordNumberSet = true
        break
      }
    }

    if (wordNumberSet) {
      return
    }

    for (let i = 0; i < this.wordsCount; i++) {
      if (this.wordsToLearn[i].remember !== true) {
        this.setWordNumber(i)
        wordNumberSet = true
        break
      }
    }

    if (!wordNumberSet) {
      this.lessonComplete = true
    }
  }

  @Watch('useFullscreen')
  useFullscreenToggle () {
    (this.$refs.fullscreen as fullscreen).toggle() // recommended
  }

  showWordTranslation () {
    store.dispatch('showWordTranslation', true)
  }
}
