import WordCharacter from '@/classes/WordCharacter'
import {appApi} from '@/store/axios'
import Word from '@/classes/Word'

export class DictionaryWord {
    word: Word
    /**
     * Была ли хоть раз показана карточка (даже без перевода)
     */
    shown: boolean
    /**
     * Был ли хоть раз показан перевод карточки
     */
    learnt: boolean
    /**
     * Было ли показан перевод слова в режиме проверки
     */
    revealed: boolean
    /**
     * Была ли карточка запомнена. true - да, false - нет, null - пока неизвестно
     */
    remember: boolean | null
    wordShownTimestamp: number | null

    translationCharacters: WordCharacter[]
    wordCharacters: WordCharacter[]

    constructor(word: Word, learnt: boolean = false) {
        this.word = word
        this.learnt = learnt
        this.shown = false
        this.remember = null
        this.revealed = false
        this.wordCharacters = []
        this.word.name.replace(/\(.*\)/gm, '')
            .trim().split('').forEach(character => {
            this.wordCharacters.push(new WordCharacter(character, character === ' '))
        })
        this.wordShownTimestamp = null

        this.translationCharacters = []
        this.word.translation.replace(/\(.*\)/gm, '')
            .trim().split('').forEach(character => {
            this.translationCharacters.push(new WordCharacter(character, character === ' '))
        })
    }

    translationCharactersShownNumber(): number {
        return this.translationCharacters
            .reduce((accumulator, currentValue) => {
                return accumulator + (currentValue.shown ? 1 : 0)
            }, 0)
    }

    wordCharactersShownNumber(): number {
        return this.wordCharacters
            .reduce((accumulator, currentValue) => {
                return accumulator + (currentValue.shown ? 1 : 0)
            }, 0)
    }

    isWordRevealed(): boolean {
        return this.wordCharacters.length === this.wordCharactersShownNumber() ||
            this.translationCharacters.length === this.translationCharactersShownNumber()
    }

    revealWord() {
        this.wordCharacters.forEach(character => {
            character.shown = true
        })
        this.translationCharacters.forEach(character => {
            character.shown = true
        })
        this.revealed = true
        this.wordShownTimestamp = (new Date()).getTime()
    }

    unrevealWord() {
        this.wordCharacters.forEach(character => {
            character.shown = false
        })
        this.translationCharacters.forEach(character => {
            character.shown = false
        })
        this.revealed = false
    }

    flushWord() {
        this.unrevealWord()
        this.shown = false
        this.remember = null
    }

    banWord(sendRequestToServer: boolean) {
        this.revealed = true
        this.shown = true
        this.remember = true
        this.learnt = true
        this.word.banned = true

        if (sendRequestToServer) {
            return appApi.post('/dictionary/mark-words/', {
                words: [this.word.pk]
            })
        }
    }

    updateWordState(delay: number, customGrade: number | null = null) {
        return this.updateWordQuery(this.word.pk,
            this.remember === null ? true : this.remember,
            this.hintsShown(),
            delay, customGrade
        )
    }

    hintsShown() {
        const wordsHintsShown = this.wordCharacters
            .reduce((accumulator, currentValue) => {
                return accumulator + (currentValue.shownAsHint ? 1 : 0)
            }, 0)

        const translationHintsShow = this.translationCharacters
            .reduce((accumulator, currentValue) => {
                return accumulator + (currentValue.shownAsHint ? 1 : 0)
            }, 0)

        return wordsHintsShown + translationHintsShow
    }


    protected updateWordQuery(pk: number, grade: boolean,
                              mistakes: number, delay: number, customGrade: number | null = null) {
        return appApi.post('/dictionary/update-words/', {
            words: [{pk, grade, mistakes, delay, customGrade}]
        })
    }


}
