








































import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import lessonsStore from '@/store/modules/lessons'
import store from '@/store/store'
import {ILesson} from '@/store/types'

@Component({})
export default class LessonList extends Vue {
    lessonsLoaded: boolean = false
    showLessonList: boolean = false

    toggleShowLessonList() {
        this.showLessonList = !this.showLessonList
    }

    get lessons() {
        return lessonsStore.lessons
    }

    get isUserAuthenticated() {
        return lessonsStore.isAuthenticated
    }

    isDemoUserAllowed(lesson: ILesson) {
        return lesson.demo && !lessonsStore.isAuthenticated
    }

    setLesson(lesson: ILesson) {
        this.showLessonList = false
        store.dispatch('setCurrentLesson', lesson)
    }

    get lesson() {
        return store.state.currentLesson
    }

    get lessonNumber() {
        return store.state.currentLessonNumber
    }

    async mounted() {
        await lessonsStore.loadLessons()
        await store.dispatch('setIsAppInitialized', true)
        await this.$nextTick()
        if (!this.isUserAuthenticated) {
            const regex = /\/lecon-(\d+)\//gm
            const match = regex.exec(window.location.href)
            if (match !== null) {
                const lesson = lessonsStore.lessons[parseInt(match[1], 10) - 1]
                if (lesson) {
                    this.setLesson(lesson)
                }
            } else {
                this.setLesson(lessonsStore.lessons[0])
            }
        }
        this.lessonsLoaded = true
    }
}
