import {VuexModule, Module, getModule, Action, Mutation} from 'vuex-module-decorators'
import store from '@/store/store.ts'
import {WordData, WordsResponse} from '@/store/types'
import {appApi} from '@/store/axios'
import Word from '@/classes/Word'

@Module({
    namespaced: true,
    name: 'WordsModule',
    store,
    dynamic: true
})

class WordsModule extends VuexModule {
    words: Word[] = []
    errors: object[] = []

    @Mutation
    SET_WORDS_DATA(wordsData: WordsResponse) {
        this.words = wordsData.words.map(wordData => {
            return new Word(wordData)
        })
        this.errors = wordsData.errors
    }

    @Mutation
    SET_ERRORS(errors: object[]) {
        this.errors = errors
    }

    @Action({ commit: 'SET_WORDS_DATA' })
    async loadWords(lessonNumber: number) {
        const url = store.state.dictionaryMode === 'repeat' ? '/dictionary/repeat-words/' :
            '/dictionary/words/' + lessonNumber + '/'
        const response = await appApi.get(url)
        return (response.data as WordsResponse)
    }

    @Action({ commit: 'SET_ERRORS' })
    async setErrors(errors: object[]) {
        return errors
    }
}
export default getModule(WordsModule)
