import {VuexModule, Module, getModule, Action, Mutation} from 'vuex-module-decorators'
import store from '@/store/store.ts'
import {IDictionaryProgressResponse, ILesson} from '@/store/types'
import {appApi} from '@/store/axios'

@Module({
    namespaced: true,
    name: 'LessonsModule',
    store,
    dynamic: true
})
export class LessonsModule extends VuexModule {
    lessons: ILesson[] = []
    isAuthenticated: boolean = false

    @Mutation
    setProgress(progress: IDictionaryProgressResponse) {
        this.lessons = progress.packets
        this.isAuthenticated = progress.isAuthenticated
    }

    @Action({ commit: 'setProgress' })
    async loadLessons() {
        const response = await appApi.get('/dictionary/progress')
        return (response.data as IDictionaryProgressResponse)
    }
}
export default getModule(LessonsModule)
