import { VerbData, VerbForm } from '@/store/types'


export default class Verb implements VerbData {
  verb: string = ''
  forms: VerbForm[]
  isTranslation: boolean = false
  packet: number = 0
  pollyUrl: string = ''
  trPollyUrl: string = ''
  translation: string = ''
  banned: boolean = false

  constructor (data: VerbData) {
    this.verb = data.verb
    this.forms = data.forms
    this.isTranslation = data.isTranslation
    this.packet = data.packet
    this.pollyUrl = data.pollyUrl
    this.trPollyUrl = data.trPollyUrl
    this.translation = data.translation
  }
}
