























import store from '../store/store'
import {Component, Vue} from 'vue-property-decorator'

@Component({})
export default class DictionaryModeSetter extends Vue {

  get dictionaryMode() {
    return store.state.dictionaryMode
  }

  setDictionaryMode(dictionaryMode: string) {
    store.dispatch('setDictionaryMode', dictionaryMode)
  }

  get repeatWordsCount () {
    return store.state.repeatWordsCount
  }
}
