import { Action, getModule, Module, Mutation, VuexModule } from 'vuex-module-decorators'
import store from '@/store/store.ts'
import { VerbsResponse } from '@/store/types'
import { appApi } from '@/store/axios'
import Verb from '@/classes/Verb'

@Module({
  namespaced: true,
  name: 'VerbsModule',
  store,
  dynamic: true
})

class VerbsModule extends VuexModule {
  verbs: Verb[] = []
  errors: object[] = []

  @Mutation
  SET_VERBS_DATA (verbsData: VerbsResponse) {
    this.verbs = verbsData.verbs.map(verbData => {
      return new Verb(verbData)
    })
    this.errors = verbsData.errors
  }

  @Mutation
  SET_ERRORS (errors: object[]) {
    this.errors = errors
  }

  @Action({ commit: 'SET_VERBS_DATA' })
  async loadVerbs (lessonNumber: number) {
    const response = await appApi.get(`/dictionary/verbs/${lessonNumber}/`)
    return (response.data as VerbsResponse).verbs
  }

  @Action({ commit: 'SET_ERRORS' })
  async setErrors (errors: object[]) {
    return errors
  }
}

export default getModule(VerbsModule)
