


















































































































import {Component, Prop, Vue, Watch} from 'vue-property-decorator'
import store from '@/store/store'
// @ts-ignore
import {Howl} from 'howler'
import {DictionaryWord} from '@/classes/DictionaryWord'
import Timer from 'easytimer.js'
import jquery from 'jquery'
import WordCharacter from '@/classes/WordCharacter'
import Word from '@/classes/Word'

@Component({})
export default class WordCard extends Vue {
  @Prop() public readonly dictionaryWord: DictionaryWord | undefined
  @Prop() public readonly learntWordsCount!: number
  translateTimerEnabled: boolean = false
  translateTimerSecondsPassed: number = 0
  translateTimerInterval: number = 1
  showTranslation: boolean = false
  soundEnabled: boolean = true
  soundOfTranslation: Howl
  soundOfWord: Howl
  timer!: Timer
  changeWordTimer!: Timer
  lastFadeoutAnimationStarted!: number
  showCharacterPlaceholders: boolean = false
  settingsPopoverOpened: boolean = false
  translationDirection: string = 'fr-ru'
  wordSoundsInitialized: boolean = false
  soundOfWordPlayed: boolean = false
  soundOfTranslationPlayed: boolean = false

  get wordId() {
    return store.state.currentLesson + '-' + this.wordNumber
  }

  get word(): Word | undefined {
    if (!this.dictionaryWord) {
      return undefined
    }
    return this.dictionaryWord.word
  }

  get wordSexClass() {
    if (!this.word) {
      return {}
    }

    return {
      'sex-male': this.word.gender === 'm',
      'sex-female': this.word.gender === 'f',
      'sex-none': this.word.gender === null
    }
  }

  get wordNumber() {
    return store.state.wordNumber
  }

  get wordsCount() {
    return store.state.wordsCount
  }

  get wordLabel(): string {
    if (this.word === undefined) {
      return ''
    }

    if (this.translationDirection === 'ru-fr') {
      return this.word.translation
    }

    return this.word.name
  }

  get wordTranslation(): string {
    if (this.word === undefined) {
      return ''
    }

    if (this.translationDirection === 'ru-fr') {
      return this.word.name
    }

    return this.word.translation
  }

  get wordCharacters(): WordCharacter[] {
    if (this.dictionaryWord === undefined) {
      return []
    }

    return this.dictionaryWord.wordCharacters
  }

  get wordChangeInterval(): number {
    return store.state.wordChangeInterval
  }

  get isShowWordTranslation() {
    return store.state.showWordTranslation
  }

  @Watch('word')
  wordChanged() {
    this.showCharacterPlaceholders = false
    this.showTranslation = false
    this.translateTimerSecondsPassed = 0
    this.wordSoundsInitialized = false
    this.soundOfWordPlayed = false
    this.soundOfTranslationPlayed = false

    if (this.soundOfTranslation) {
      this.soundOfTranslation.stop()
    }

    if (this.soundOfWord) {
      this.soundOfWord.stop()
    }

    this.timer.stop()
    this.changeWordTimer.stop()

    // if (this.cardMode === 'self-check') {
    //   this.beforeTimerStarted()
    // } else {
    //   if (this.translateTimerEnabled) {
    //     this.toggleTimer(true)
    //   }
    // }

    if (this.dictionaryWord) {
      this.dictionaryWord.shown = true
    }
  }

  @Watch('dictionaryWord.revealed')
  dictionaryWordChanged(isWordRevealed: boolean) {
    if (isWordRevealed && this.soundEnabled) {
      this.playSound(false)
    }
  }

  @Watch('cardMode')
  cardModeChanged(cardMode: string) {
    this.translateTimerEnabled = false
    this.wordChanged()
  }

  toggleSoundEnabled() {
    this.soundEnabled = !this.soundEnabled
  }

  showHint() {
    if (this.dictionaryWord === undefined) {
      return
    }

    if (!this.showCharacterPlaceholders) {
      this.showCharacterPlaceholders = true
      return
    }

    for (const i in this.wordCharacters) {
      if (!this.wordCharacters[i].shown) {
        this.wordCharacters[i].show(true)
        break
      }
    }

    this.dictionaryWord.revealed = this.dictionaryWord.isWordRevealed()
  }

  playSoundOfWord(): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      const audio = this.soundOfWord
      if (!this.soundEnabled || this.soundOfWordPlayed) {
        resolve()
        return
      }

      if (audio) {
        audio.once('end', () => {
          this.soundOfWordPlayed = true
          resolve()
        })
        audio.play()
      }
    })
  }

  playSoundOfTranslation(): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      const audio = this.soundOfTranslation
      if (!this.soundEnabled || this.soundOfTranslationPlayed) {
        resolve()
        return
      }

      this.soundOfWord.pause()
      if (audio) {
        audio.once('end', () => {
          this.soundOfTranslationPlayed = true
          resolve()
        })
        audio.play()
      }

    })
  }

  toggleFullscreen() {
    store.dispatch('setFullscreenMode', !store.state.useFullscreen)
  }

  showWordTranslation(): void {
    // if (this.showTranslation || (this.cardMode === 'learn' && !this.translateTimerEnabled)) {
    //   store.dispatch('setWordNumber', store.state.wordNumber + 1)
    //   return
    // }

    this.showTranslation = true
    this.translateTimerSecondsPassed = this.translateTimerInterval
    if (this.dictionaryWord) {
      this.dictionaryWord.learnt = true
    }

    this.playSound(true).then(() => {
      // if (this.cardMode !== 'learn') {
      //   return
      // }

      this.changeWordTimer.start({
        precision: 'seconds',
        startValues: {seconds: 0},
        target: {seconds: this.wordChangeInterval}
      })
      if (!this.translateTimerEnabled) {
        this.changeWordTimer.pause()
      }
    })
  }

  setWordChangeInterval(event: any) {
    store.dispatch('setWordChangeInterval', parseInt(event.target.value, 10))
  }

  changeTranslateTimerInterval(): boolean {
    if (++this.translateTimerInterval > 5) {
      this.translateTimerInterval = 1
      return false
    }

    return true
  }

  beforeTimerStarted(): Promise<void> {
    if (!this.wordSoundsInitialized) {
      this.wordSoundsInitialized = true
      if (this.soundEnabled) {
        // return this.playSound(this.cardMode !== 'learn')
      }
    }

    return new Promise<void>(resolve => resolve())
  }

  playSound(soundOfTranslation: boolean = false) {
    // if ((soundOfTranslation && this.translationDirection === 'fr-ru') ||
    //   (!soundOfTranslation && this.translationDirection === 'ru-fr' && this.cardMode === 'learn')) {
    //   return this.playSoundOfTranslation()
    // }

    return this.playSoundOfWord()
  }

  changeWord() {
    if (!this.translateTimerEnabled) {
      return
    }

    store.dispatch('setWordNumber', store.state.wordNumber + 1)
  }

  toggleTimer(forceTimerStart: boolean = false) {
    // if (this.cardMode !== 'learn') {
    //   return
    // }

    this.translateTimerEnabled = forceTimerStart || !this.translateTimerEnabled

    if (!this.translateTimerEnabled) {
      if (this.timer.isRunning()) {
        this.timer.pause()
      }

      if (this.changeWordTimer.isRunning()) {
        this.changeWordTimer.pause()
      }

      return
    }

    if (this.changeWordTimer.isPaused()) {
      this.changeWordTimer.start()
    } else if (this.timer.isPaused()) {
      this.timer.start()
    } else {
      this.beforeTimerStarted().then(() => {
        if (!this.translateTimerEnabled) {
          return
        }
        this.timer.start({
          precision: 'seconds',
          startValues: {seconds: this.translateTimerSecondsPassed},
          target: {seconds: this.translateTimerInterval}
        })
      })
    }
  }

  @Watch('isShowWordTranslation')
  eventShowWordTranslation(show: boolean) {
    if (show) {
      this.showWordTranslation()
      store.dispatch('showWordTranslation', false)
    }
  }

  beforeDestroy() {
    this.timer.stop()
    this.changeWordTimer.stop()
  }

  mounted() {
    const lessonNumber = store.state.currentLesson
    this.timer = new Timer()
    this.timer.addEventListener('secondsUpdated', (e) => {
      this.translateTimerSecondsPassed++
    })
    this.timer.addEventListener('started', (e) => {
      this.translateTimerEnabled = true
    })
    this.timer.addEventListener('targetAchieved', (e) => {
      if (!this.translateTimerEnabled) {
        return
      }
      this.showWordTranslation()
    })

    this.changeWordTimer = new Timer()
    this.changeWordTimer.addEventListener('targetAchieved', (e) => {
      if (lessonNumber !== store.state.currentLesson) {
        return
      }
      // if (this.cardMode === 'learn') {
      //   this.changeWord()
      // }
    })

    this.lastFadeoutAnimationStarted = Date.now()
    jquery('.word-card').off('mousemove')
    jquery('.word-card').on('mousemove', () => {
      if (Date.now() - this.lastFadeoutAnimationStarted < 1000) {
        return
      }
      this.lastFadeoutAnimationStarted = Date.now()
      const el = jquery('.word-card:hover .invis')
      el.removeClass('run-fadeout-animation')
      if (el[0]) {
        void el[0].offsetWidth // tslint:disable-line
      }
      el.addClass('run-fadeout-animation')
    })

    document.body.addEventListener('keydown', (e) => {
      if (e.keyCode === 32) {
        this.beforeTimerStarted().then(() => this.toggleTimer())
      }
    })

    this.wordChanged()
  }
}
