


























import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import LessonList from '@/components/LessonList.vue'
import store from '@/store/store'


@Component({
  components: {
    LessonList
  }
})
export default class App extends Vue {
  @Prop(String) readonly packetPk!: string
  @Prop(String) readonly mode!: string

    get isDevEnv() {
        return store.state.isDevEnv
    }

    async mounted() {

      if (!this.packetPk) {
        return
      }
      store.dispatch('setDictionaryMode', this.mode || 'learn')
      await store.dispatch('setLessonByPk', parseInt(this.packetPk, 10))
      store.dispatch('loadRepeatWordsCount')
      store.dispatch('setIsAppInitialized', true)
    }
}
