

































































import { Component, Vue, Watch } from 'vue-property-decorator'
import LearnWordsDictionary from '@/components/Dictionaries/LearnWordsDictionary.vue'
import RepeatWordsDictionary from '@/components/Dictionaries/RepeatWordsDictionary.vue'
import store from '@/store/store'
import { appApi } from '@/store/axios'
import BlankCard from '@/components/BlankCard.vue'
import lessonsStore from '@/store/modules/lessons'
import SelfCheckWordsDictionary from '@/components/Dictionaries/SelfCheckWordsDictionary.vue'
import { DictionaryWord } from '@/classes/DictionaryWord'
import wordsStore from '@/store/modules/words'
import VerbsDictionary from '@/components/Dictionaries/VerbsDictionary.vue'

@Component({
  components: { VerbsDictionary, SelfCheckWordsDictionary, RepeatWordsDictionary, LearnWordsDictionary, BlankCard }
})
export default class LearnPage extends Vue {
  notEnoughMoney: boolean = false
  error: object | null = null
  wordsToLearn: DictionaryWord[] = []
  wordsAreLoading: boolean = false
  loginWarnAccepted: boolean = false

  get isAppInitialized () {
    return store.state.isAppInitialized
  }

  get isAuthenticated () {
    return !this.currentLesson ? false : this.currentLesson.isAuthenticated
  }

  get dictionaryMode () {
    return this.$store.state.dictionaryMode
  }

  get isCurrentLessonAdded () {
    return this.currentLesson &&
      (this.currentLesson.added || (this.currentLesson.demo && !lessonsStore.isAuthenticated))
  }

  get currentLesson () {
    return store.state.currentLesson
  }

  @Watch('dictionaryMode')
  function () {
    // console.log('dictionary mode changed')
    this.initDictionary()
  }

  @Watch('currentLesson')
  async lessonChanged () {
    this.$forceUpdate()
    this.notEnoughMoney = false
    if (this.currentLesson && this.isCurrentLessonAdded) {
      await this.$nextTick()
      this.initDictionary()
    }
  }

  async loadLessonWords () {
    // this.wordsAreLoading = true
    await this.$nextTick()
    this.wordsToLearn = []
    // console.log('Loading words...')
    await wordsStore.loadWords(store.state.currentLessonNumber)
    console.log(wordsStore.words)
    wordsStore.words.forEach(word => this.wordsToLearn.push(new DictionaryWord(word)))
    await this.$nextTick()
    // this.wordsAreLoading = false
  }

  async addLessonPack () {
    const response = await appApi.post('/dictionary/add-packets/', {
      packets: [ store.state.currentLessonNumber ],
      activate: true
    })
    if (!response.data.errors.length || !this.isAuthenticated) {
      this.currentLesson!.added = true
      await this.$forceUpdate()
      this.initDictionary()
    } else {
      this.error = response.data.errors[0]
      this.$forceUpdate()
    }
  }

  async initDictionary () {
    if (!this.isAuthenticated && !this.loginWarnAccepted){
      return
    }
    console.log('Init dictionary')
    wordsStore.setErrors([])
    await this.$nextTick()
    await this.$nextTick()

    if (['learn', 'repeat', 'verbs'].find(key => key === this.dictionaryMode)) {
      await this.loadLessonWords()
    }

    if (this.dictionaryMode === 'learn') {
      await (this.$refs.learnDictionary as LearnWordsDictionary).initDictinonary(this.wordsToLearn)
    } else if (this.dictionaryMode === 'repeat') {
      await (this.$refs.repeatDictionary as RepeatWordsDictionary).initDictinonary()
    } else if (this.dictionaryMode === 'self-check') {
      await (this.$refs.selfCheckDictionary as SelfCheckWordsDictionary).initDictinonary(this.wordsToLearn)
    } else if (this.dictionaryMode === 'verbs') {
      await (this.$refs.verbsDictionary as VerbsDictionary).initDictinonary()
    }
  }

  unselectLesson () {
    store.dispatch('setCurrentLesson', null)
  }
}
