import Vue from 'vue'
import Vuex, { StoreOptions } from 'vuex'
import { ILesson, RootState } from '@/store/types'
import { appApi } from '@/store/axios'

Vue.use(Vuex)

const store: StoreOptions<RootState> = {
  state: {
    currentLesson: null,
    currentLessonNumber: 0,
    slideTimerInterval: 1,
    wordNumber: 0,
    wordsCount: 0,
    wordChangeInterval: 1,
    useFullscreen: false,
    isAppInitialized: false,
    showWordTranslation: false,
    isDevEnv: process.env.NODE_ENV === 'development',
    dictionaryMode: 'learn',
    repeatWordsCount: 0,
  },
  mutations: {
    SET_CURRENT_LESSON (state: RootState, lesson: ILesson) {
      state.currentLesson = lesson
      state.currentLessonNumber = lesson ? lesson.pk : 0
    },
    SET_TIMER_INTERVAL (state: RootState, interval: number) {
      state.slideTimerInterval = interval
    },
    SET_WORD_CHANGE_INTERVAL (state: RootState, interval: number) {
      state.wordChangeInterval = interval
    },
    SET_WORD_NUMBER (state: RootState, wordNumber: number) {
      state.wordNumber = wordNumber
    },
    SET_WORDS_COUNT (state: RootState, wordsCount: number) {
      state.wordsCount = wordsCount
    },
    SET_FULLSCREEN_MODE (state: RootState, useFullscreen: boolean) {
      state.useFullscreen = useFullscreen
    },
    SET_SHOW_WORD_TRANSLATION (state: RootState, showWordTranslation: boolean) {
      state.showWordTranslation = showWordTranslation
    },
    SET_IS_APP_INITIALIZED (state: RootState, isAppInitialized: boolean) {
      state.isAppInitialized = isAppInitialized
    },
    SET_DICTIONARY_MODE (state: RootState, dictionaryMode: string) {
      state.dictionaryMode = dictionaryMode
    },
    SET_REPEAT_WORDS_COUNT (state: RootState, repeatWordsCount: number) {
      state.repeatWordsCount = repeatWordsCount
    }
  },
  actions: {
    setCurrentLesson ({ commit }, lesson: ILesson) {
      commit('SET_CURRENT_LESSON', lesson)
    },
    setSlideTimerInterval ({ commit }, interval: number) {
      if (interval > 5) {
        interval = 1
      }

      commit('SET_TIMER_INTERVAL', interval)
      // commit('SET_CARD_MODE', 'learn')
    },
    setWordChangeInterval ({ commit }, interval: number) {
      if (interval > 5) {
        interval = 1
      }

      commit('SET_WORD_CHANGE_INTERVAL', interval)
    },
    setWordNumber (context, wordNumber: number) {
      if (wordNumber >= context.state.wordsCount || wordNumber < 0) {
        wordNumber = 0
      }
      context.commit('SET_WORD_NUMBER', wordNumber)
    },
    setWordsCount ({ commit }, wordsCount: number) {
      commit('SET_WORDS_COUNT', wordsCount)
    },
    setFullscreenMode ({ commit }, useFullscreen: boolean) {
      commit('SET_FULLSCREEN_MODE', useFullscreen)
    },
    showWordTranslation ({ commit }, setShowWordTranslation: boolean) {
      commit('SET_SHOW_WORD_TRANSLATION', setShowWordTranslation)
    },
    setIsAppInitialized ({ commit }, isAppInitialized: boolean) {
      commit('SET_IS_APP_INITIALIZED', isAppInitialized)
    },
    async setLessonByPk ({ commit }, lessonNumber: number) {
      const response = await appApi.get('/dictionary/progress/' + lessonNumber + '/')
      return await this.dispatch('setCurrentLesson', response.data)
    },
    setDictionaryMode ({ commit }, dictionaryMode: string) {
      commit('SET_DICTIONARY_MODE', dictionaryMode)
    },
    async loadRepeatWordsCount ({ commit }) {
      const response = await appApi.get('/dictionary/repeat-words/count/')
      commit('SET_REPEAT_WORDS_COUNT', response.data.count)
    }
  },
}

export default new Vuex.Store<RootState>(store)
