
export default class WordCharacter {
    character: string
    shown: boolean
    shownAsHint: boolean

    constructor(character: string, shown: boolean = false, shownAsHint: boolean = false) {
        this.character = character
        this.shown = shown
        this.shownAsHint = shownAsHint
    }

    show(showAsHint: boolean) {
        this.shown = true
        this.shownAsHint = showAsHint
    }
}
