import {VuexModule, Module, getModule, Action, Mutation} from 'vuex-module-decorators'
import store from '@/store/store.ts'
import {IDictionaryProgressResponse, ILesson} from '@/store/types'
import {appApi} from '@/store/axios'

@Module({
    namespaced: true,
    name: 'SettingsModule',
    store,
    dynamic: true
})
export class SettingsModule extends VuexModule {
    translateTimerInterval: number = 1
    soundEnabled: boolean = true
    translationDirection: string = 'fr-ru'
    translationType: string = 'original'
    translationDisabled: boolean = false

    @Mutation
    SET_SOUND_ENABLED(enabled: boolean) {
        this.soundEnabled = enabled
    }

    @Mutation
    SET_TRANSLATION_DIRECTION(direction: string) {
        this.translationDirection = direction
    }

    @Mutation
    SET_TRANSLATE_TIMER_INTERVAL(interval: number) {
        this.translateTimerInterval = interval > 5 ? 1 : interval
    }

    @Mutation
    SET_TRANSLATION_TYPE(type: string) {
        this.translationType = type
    }

    @Mutation
    SET_TRANSLATION_DISABLED(disabled: boolean) {
        this.translationDisabled = disabled
    }

    @Action({commit: 'SET_SOUND_ENABLED'})
    async setSoundEnabled(enabled: boolean) {
        return enabled
    }

    @Action({commit: 'SET_TRANSLATE_TIMER_INTERVAL'})
    async setTranslateTimerInterval(interval: number) {
        return interval
    }

    @Action({commit: 'SET_TRANSLATION_DIRECTION'})
    async setTranslationDirection(direction: string) {
        return direction
    }

    @Action({commit: 'SET_TRANSLATION_TYPE'})
    async setTranslationType(type: string) {
        return type
    }

    @Action({commit: 'SET_TRANSLATION_DISABLED'})
    async setTranslationDisabled(disabled: boolean) {
        return disabled
    }
}

export default getModule(SettingsModule)
